import React, { useState } from 'react';
import { DndProvider } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';

import WordTree from './WordTree';
import WordBox from './WordBox';
import OpenPinterestButton from './OpenPinterestButton';

const MainPanel = () => {
	const [searchTerms, setSearchTerms] = useState([]);

	const handleDrop = (curTerms) => {
		setSearchTerms(curTerms);
	};

	// useEffect(() => {
	// 	// You can perform any additional logic when the searchTerms change,
	// 	// like updating other components or making API calls.
	// 	// For now, we'll just log the searchTerms.
	// 	console.log('Search Terms:', searchTerms);
	// }, [searchTerms]);

	return (
		<DndProvider backend={HTML5Backend}>
			<div style={{ display: 'flex', height: '100vh' }}>
			
			<div style={{ flex: '0 0 40%' }}>
				<WordTree onDragStart={() => {}}/>
			</div>

			<div style={{ flex: '0 0 40%' }}>
				<WordBox onDrop={handleDrop} terms={searchTerms} setTerms={setSearchTerms} />
			</div>

			<div style={{ flex: '0 0 20%' }}>
				<OpenPinterestButton searchTerms={searchTerms} />
			</div>
			</div>
		</DndProvider>
	);
};

export default MainPanel;
