import React from 'react';
import { Box, ButtonBase, Typography, styled } from '@mui/material';
import './General.css';

const OpenPinterestButton = ({ searchTerms }) => {
  const handleButtonClick = () => {
    const pinterestUrl = `https://www.pinterest.com/search/pins/?q=${encodeURIComponent(
      searchTerms.join('+')
    )}`;
    window.open(pinterestUrl, '_blank');
  };

  const BigButton = styled(ButtonBase)(({ theme }) => ({
    height: '95vh', width: '100%', 
    position: 'relative',
    alignItems: 'flex-start',
    backgroundColor: 'rgb(255, 100, 100)',
    '&:hover, &.Mui-focusVisible': {
      zIndex: 1, backgroundColor: 'rgb(255, 120, 120)',
    },
  }));
  return (
    <Box sx= {{pb: 2, pt: 2, pl: 1, pr: 2}}>
      <BigButton sx={{ pt: 1, borderRadius: 2, display: 'flex' }} onClick={handleButtonClick}>
          <Typography sx={{ ml: 2, color: 'white', marginRight: 'auto' }} variant="h5" component="div">
          3. Go!
          </Typography>
      </BigButton>
    </Box>
  );
};

export default OpenPinterestButton;