import { useDrag } from 'react-dnd';
import { ItemTypes } from './Constants';
import { Box, TextField, Typography } from '@mui/material';

function DraggableWord(props) {
	const [{ isDragging }, drag] = useDrag(() => ({
		type: ItemTypes.TERM,
		collect: monitor => ({
			isDragging: !!monitor.isDragging(),
		}),
		item: { term: props.term },
	}));

	const keyDownHandler = (e) => {
		if (e.key === 'Enter') {
			props.setEditing(false);
		}
	}

	const inner = props.editing ?
		<TextField autoFocus value={props.term}
			onChange={(e) => props.setTerm(e.target.value)}
			onBlur={() => props.setEditing(false)}
			onKeyDown={keyDownHandler}
			sx={{ "& input": { padding: '0.2em 0.5em', fontSize: '1.2em' } }}
		/>
		: <Typography style={{ opacity: isDragging ? 0.5 : 1, cursor: 'move', fontSize: '1em' }}>
			{props.term.length === 0 ? '(empty)' : props.term }
		</Typography>;
	return <Box sx={{ borderRadius: 2, color: 'rgb(38, 38, 38)' }} ref={drag} className="node-heading-text">
		{inner}
	</Box>
}

export default DraggableWord;
