import React, { Component } from 'react';
import { ExpandMore, ChevronRight, Edit, Lightbulb } from '@mui/icons-material';
import Icon from '@material-ui/core/Icon';
import DraggableWord from './DraggableWord';
import './General.css'
import { Box, Button, CircularProgress, IconButton, Menu, MenuItem } from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';

const GenState = Object.freeze({
	DIRTY: Symbol("dirty"),
	WAITING: Symbol("waiting"),
	DONE: Symbol("done")
});

function interp(initial, end, proportion) {
	return initial + (end - initial) * proportion;
}

class WordNode extends Component {
	constructor(props) {
		super(props);
		const initialTerm = props.initialText || '';
		this.state = {
			children: [],
			collapse: true,
			editing: false,
			genState: GenState.DIRTY,
			term: initialTerm,
			allTerms: props.initialParentTerms.concat([initialTerm]),
			parentTerms: props.initialParentTerms,
			suggestions: [],
			anchorEl: null,
		}
		this.childRefs = [];
		this.index = React.createRef(0);
	}

	componentDidUpdate() {
		const newAllTerms = this.state.parentTerms.concat(this.state.term);
		if (newAllTerms.toString() !== this.state.allTerms.toString()) {
			this.setState({  allTerms: newAllTerms });

			for (const childRef of this.childRefs) {
				childRef.current.updateParentTerms(newAllTerms);
			}
		}
	}

	updateParentTerms(newParentTerms) {
		this.setState({ parentTerms: newParentTerms, genState: GenState.DIRTY });
	}

	render() {
		const appendChild = (initialText='') => {
			const childRef = React.createRef();
			const thisKey = this.index.current++;
			const newChild = <WordNode
				key={thisKey}
				idKey={thisKey}
				initialText={initialText}
				depth={this.props.depth - 1} maxDepth={this.props.maxDepth}
				initialParentTerms={this.state.allTerms}
				ref={childRef}
				handleDelete={() => {
					const idx = this.childRefs.findIndex((ref) => ref.current.props.idKey === thisKey);
					if (idx === -1)
						return;
					this.childRefs = this.childRefs.slice(0, idx).concat(this.childRefs.slice(idx + 1));
					this.setState((prevState) => {
						return {children: prevState.children.slice(0, idx).concat(prevState.children.slice(idx + 1))};
					});
				}}
			>
			</WordNode>;
			this.childRefs.push(childRef);
			this.setState((prevState) => { return { children: [...prevState.children, newChild] } });
		};

		const handleEdit = () => {
			this.setState({ editing: true });
		};

		const handleGenerate = (evt) => {
			if (this.state.genState !== GenState.DIRTY) {
				this.setState({ anchorEl: evt.currentTarget });
				return;
			}

			const PINGEN_URL = 'https://pingenerate.azurewebsites.net/api/pingenerate';
			const queryTerms = this.state.allTerms
				.slice(1)
				.map((term) => term.trim())
				.filter((term) => term.length > 0);

			if (queryTerms.length === 0) {
				alert('Please supply at least one term');
				return;
			}

			const body = JSON.stringify({ input_words: queryTerms });
			console.log('Sending suggestions request:', body);

			fetch(PINGEN_URL, {
				method: "POST", // *GET, POST, PUT, DELETE, etc.
				mode: "cors", // no-cors, *cors, same-origin
				// cache: "no-cache", // *default, no-cache, reload, force-cache, only-if-cached
				credentials: "same-origin", // include, *same-origin, omit
				headers: {
					"Content-Type": "application/json",
				},
				redirect: "follow", // manual, *follow, error
				referrerPolicy: "no-referrer", // no-referrer, *no-referrer-when-downgrade, origin, origin-when-cross-origin, same-origin, strict-origin, strict-origin-when-cross-origin, unsafe-url
				body
			}).then(response => {
				if (response.status !== 200) {
					throw response.text();
				}
				return response.text();
			}).then(suggestionsStr => {
				console.log('Received suggestions str:', suggestionsStr);

				const sugsWhitespace = suggestionsStr.split(',');
				// Strip leading and trailing whitespaces from each element
				const sugsTrimmed = sugsWhitespace.map(item => item.trim());
				if (sugsTrimmed.length !== 0) {
					const lastStr = sugsTrimmed[sugsTrimmed.length - 1];
					if (lastStr.endsWith('.')) {
						sugsTrimmed[sugsTrimmed.length - 1] = lastStr.substring(0, lastStr.length - 1);
					}
				}
				console.log('parsed suggestions:', sugsTrimmed);
				this.setState({ genState: GenState.DONE, suggestions: sugsTrimmed });
			}).catch(error => {
				if (error instanceof Promise) {
					error.then((error) => {
						alert(`API error!\n${error}`)
						console.log(error);
					});
				} else {
					alert(`Internal error! Please report the following to garygengxiao@gmail.com:\n${error}`)
					console.log(error);

				}
			});

			this.setState({ genState: GenState.WAITING, anchorEl: evt.currentTarget });
		};

		const handleMenuClose = (suggestion) => {
			appendChild(suggestion);
			this.setState({ anchorEl: null, collapse: false });
		}

		const isNotLeaf = (this.props.depth > 0);
		const adderButton = <Button sx={{ mt: 0.5 }} onClick={() => appendChild()} variant="text"><b>Add Word</b></Button>;

		if (this.props.root) {
			return <Box>
				{this.state.children}
				{adderButton}
			</Box>
		}

		const proportion = (this.props.maxDepth - this.props.depth) / this.props.maxDepth;
		const colorValue = interp(230, 190, proportion);

		const menuItems = this.state.suggestions.map((s, idx) => <MenuItem key={idx} onClick={() => handleMenuClose(s)}>{ s }</MenuItem>);
		return (
			<Box sx={{ margin: 0.5, padding: 0.1, borderRadius: 2 }} style={{ backgroundColor: `rgb(255, ${colorValue}, ${colorValue})` }}>
				<Box className="node-heading" sx={{ margin: 1 }}>
					{isNotLeaf ?
						<Box onClick={() => this.setState((prev) => { return { collapse: !prev.collapse } })} style={{ display: 'inline' }}>
							{this.state.collapse ? <ChevronRight className='collapse-icon' /> : <ExpandMore className='collapse-icon' />}
						</Box>
						: <Icon className='collapse-icon' />}
					<DraggableWord key={this.state.term} term={this.state.term} setTerm={(term) => this.setState({ term })}
						editing={this.state.editing}
						setEditing={(editing) => this.setState({ editing })} />
					<IconButton onClick={handleEdit} sx={{ ml: 2 }}>
						<Edit />
					</IconButton>
					<IconButton onClick={handleGenerate} >
						<Lightbulb />
					</IconButton>
					<IconButton edge="end" aria-label="delete" onClick={this.props.handleDelete}>
						<DeleteIcon />
					</IconButton>
					{this.state.genState === GenState.WAITING && <CircularProgress style={{ color: 'rgba(0, 0, 0, 0.54)' }} sx={{ ml: 'auto', mr: 3 }} size='1.3rem' />}
				</Box>
				<Box sx={{ ml: 2, mb: 1 }} style={{ display: this.state.collapse ? 'none' : 'block' }}>
					{this.state.children}
					{isNotLeaf && adderButton}
				</Box>
				<Menu
					anchorEl={this.state.anchorEl}
					open={Boolean(this.state.anchorEl) && this.state.genState === GenState.DONE && !!this.state.suggestions}
					onClose={() => handleMenuClose()}
					MenuListProps={{
						'aria-labelledby': 'basic-button',
					}}
				>
					{menuItems}
				</Menu>
			</Box>);
	}
}

export default WordNode;
