import React from 'react';
import { useDrop } from 'react-dnd';
import { ItemTypes } from './Constants';
import { Box, IconButton, List, ListItem, ListItemSecondaryAction, TextField, Typography } from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';

const WordBox = ({ onDrop, terms, setTerms }) => {
  const [{ isOver }, drop] = useDrop({
    accept: ItemTypes.TERM,
    drop: (item) => {
      const term = item.term.trim();
      if (term.length === 0)
        return;
      // Handle the drop event and add the term to the WordBox
      const newTerms = [...terms, term];
      setTerms(newTerms);
      // Notify the parent component about the updated terms
      onDrop(newTerms);
    },
    collect: (monitor) => ({
      isOver: !!monitor.isOver(),
    }),
  });

  const handleDelete = (index) => {
    const newTerms = [...terms];
    newTerms.splice(index, 1);
    setTerms(newTerms);
  };

  const handleEdit = (index, newText) => {
    const newTerms = [...terms];
    newTerms[index] = newText;
    setTerms(newTerms);
  };

  return (
    <Box sx={{height: '93%', pt: 2, pb: 2, pl: 1, pr: 1 }}>
    <List
      ref={drop}
      sx={{
        border: '2px solid',
        borderColor: 'rgb(255, 100, 100)',
        borderRadius: 2,
        backgroundColor: isOver ? 'rgb(250, 250, 250)': 'white',
        height: '100%',
        pt: 1, pb: 1,
      }}
    >
      <Typography sx={{ ml: 2, color: 'rgb(255, 100, 100)' }} variant="h5" component="div">
      2. Stage
      </Typography>
      {terms.map((term, idx) => (
        <ListItem key={idx} >
          <TextField fullWidth value={term} onChange={(e) => handleEdit(idx, e.target.value)}
            sx={{ "& input": { padding: '0.2em 0.5em', lineHeight: '1.5', fontSize: '1.2em' } }} />
          <ListItemSecondaryAction>
            <IconButton edge="end" aria-label="delete" onClick={handleDelete}>
              <DeleteIcon />
            </IconButton>
          </ListItemSecondaryAction>
        </ListItem>
      ))}
    </List>
    </Box>
  );
};

export default WordBox;
