import React, { Component } from 'react';
import WordNode from './WordNode';
import { Box, Typography } from '@mui/material';


class WordTree extends Component {
  constructor() {
    super();
    this.latestId = 0;
  }

  render() {
    return (
      <Box sx={{ pt: 3, pb: 3, pl: 2, pr: 1 }}>
        <Typography sx={{ ml: 2, mb: 2, color: 'rgb(255, 100, 100)' }} variant="h5" component="div">
        1. Generate
        </Typography>
        <WordNode depth={5} maxDepth={5} root initialParentTerms={[]}></WordNode>
      </Box>
    );
  }
};

export default WordTree;
